<template>
  <div class="detail">
    <van-tabs scrollspy sticky>
      <!-- 商品 -->
      <van-tab title="商品">
        <div class="bannerD">
          <van-swipe @change="onChange">
            <van-swipe-item v-for=" (b,index) in bannerImg" :key="index">
              <van-image :src="b" width="100%" height="100%" fit="cover" />
            </van-swipe-item>
            <template #indicator>
              <div class="custom-indicator">{{ current + 1 }}/{{bannerImg.length}}</div>
            </template>
          </van-swipe>
          <div class="infoName">
            <div class="money">
              ￥{{storeInfo.price}}
              <span v-if="storeInfo.vip_price">
                {{storeInfo.vip_price}}
                <img src="../assets/vip.png" alt />
              </span>
            </div>
            <div class="info">{{storeInfo.store_name}}</div>
            <div class="num">
              <div>原价:￥{{storeInfo.ot_price}}</div>
              <div>库存:{{storeInfo.stock}}件</div>
              <div>销量:{{storeInfo.fsales}}件</div>
            </div>
          </div>
        </div>
      </van-tab>
      <!-- 评价 -->
      <van-tab title="评价">
        <div class="evaluate">
          <div>用户评价(0)</div>
          <div>
            <span class="redtext">0%</span>好评率
            <van-icon class="arroeR" name="arrow" />
          </div>
        </div>
      </van-tab>
      <!-- 推荐 -->
      <van-tab title="推荐">
        <div class="recommend">
          <p class="good">优品推荐</p>
          <van-swipe class="my-swipe" indicator-color="#fa7f3f" :loop="false">
            <van-swipe-item class="bItem" v-for="(n,index) in Newarr" :key="index">
              <div class="boxItem" v-for="g in n.tpyer" :key="g.id">
                <img class="boxImg" :src="g.image" />
                <div>
                  <p class="line">{{g.store_name}}</p>
                  <p class="linet">￥{{g.price}}</p>
                </div>
              </div>
            </van-swipe-item>
          </van-swipe>
        </div>
      </van-tab>
      <!-- 详情 -->
      <van-tab title="详情">
        <div class="details">
          <p class="bigtitle">产品介绍</p>
          <div class="conter" v-html="storeInfo.description"></div>
        </div>
      </van-tab>
    </van-tabs>
    <van-goods-action>
      <van-goods-action-icon icon="chat-o" text="客服" @click="onClickIcon" />
      <van-goods-action-icon icon="cart-o" text="购物车" @click="toCart" />
      <van-goods-action-icon icon="shop-o" text="店铺" @click="onClickIcon" />
      <van-goods-action-button type="warning" text="加入购物车" @click="onClickAdd" />
      <van-goods-action-button type="danger" text="立即购买" @click="onClickBuy" />
    </van-goods-action>
    <van-sku
      v-model="show"
      :sku="sku"
      :goods="goods"
      @buy-clicked="onBuyClicked"
      @add-cart="onAddCartClicked"
    />
  </div>
</template>
<script>
export default {
  name: "Detail",
  props: ["id"],
  data() {
    return {
      current: 0,
      proItem: null,
      bannerImg: [],
      storeInfo: [],
      once: 6,
      Newarr: [],
      //商品详情按钮信息
      show: false,
      sku: {
        //初始化demo数据
        // 所有sku规格类目与其值的从属关系，比如商品有颜色和尺码两大类规格，颜色下面又有红色和蓝色两个规格值。
        // 可以理解为一个商品可以有多个规格类目，一个规格类目下可以有多个规格值。
        tree: [
          {
            k: "颜色", //属性名称
            k_id: "1", //属性的ID值 唯一 在当前产品里面唯一
            k_s: "s1", //属性的k_s值 对应list里面组合键名
            v: [
              //颜色属性可用值列表
              {
                id: "30349", //属性ID
                name: "红色", //属性名
                //属性图片，默认只有第一行属性有
                imgUrl: "https://b.yzcdn.cn/vant/sku/shoes-1.png",
              },
              {
                id: "1215",
                name: "白色",
                imgUrl: "https://b.yzcdn.cn/vant/sku/shoes-1.png",
              },
            ],
          },
          {
            k: "尺寸",
            k_id: "2",
            v: [
              {
                id: "1193",
                name: "40码",
              },
              {
                id: "1194",
                name: "42码",
              },
            ],
            k_s: "s2",
            count: 2,
          },
        ],
        // 所有 sku 的组合列表，如下是：白色1、白色2、天蓝色1、天蓝色2
        list: [
          {
            id: 2259,
            price: 120, //价格
            s1: "1215", //对应属性列表里面 k_s 等于s1的属性id值
            s2: "1193",
            stock_num: 20, //库存
            goods_id: 946755, //产品ID
          },
          {
            id: 2260,
            price: 110,
            s1: "1215",
            s2: "1194",
            stock_num: 2,
            goods_id: 946755,
          },
          {
            id: 2257,
            price: 130,
            s1: "30349",
            s2: "1193",
            stock_num: 40,
            goods_id: 946755,
          },
          {
            id: 2258,
            price: 100,
            s1: "30349",
            s2: "1194",
            stock_num: 50,
            goods_id: 946755,
          },
        ],
        price: "5.00",
        stock_num: 227, // 商品总库存
        none_sku: false, // 是否无规格商品
        hide_stock: false, // 是否隐藏剩余库存
      },
      goods: {
        // 数据结构见下方文档
      },
      messageConfig: {
        // 数据结构见下方文档
      },
    };
  },
  created() {
    this.$axios.get("api/product/detail/" + this.id).then((data) => {
      if (data.data.status == 200) {
        this.bannerItem = data.data.data;
        this.storeInfo = data.data.data.storeInfo;
        this.bannerImg = data.data.data.storeInfo.slider_image;

        // 商品种类选择格式转换
        let arrs = data.data.data.productAttr;
        let listValue = data.data.data.productValue;
        //sku 数据转换
        let id = 1;
        let idobj = {};
        let ssk = 1;
        this.sku.list = [];
        //获取产品属性里面第一个值默认图片
        this.goods.picture = listValue[Object.keys(listValue)[0]].image;

        //循环产品属性 组装购物车 属性tree
        this.sku.tree = arrs.map((atr, ads) => {
          let va = atr.attr_value.map((at, index) => {
            id++;
            idobj[at.attr] = id;
            let attrs = {
              id: id,
              name: at.attr,
            };
            if (ads < 1) {
              attrs.imgUrl = listValue[Object.keys(listValue)[index]].image;
            }
            return attrs;
          });
          var ks = "s" + ssk;
          ssk++;
          return {
            k: atr.attr_name,
            k_id: atr.attr_name,
            v: va,
            k_s: ks,
          };
        });
        //循环产品属性组合,组装sku 列表
        for (let key in listValue) {
          let v = listValue[key];
          let keys = key.split(",");
          this.sku.list.push({
            id: v.unique,
            price: v.price, //价格
            s1: idobj[keys[0]],
            s2: idobj[keys[1]],
            stock_num: v.stock, //库存
            goods_id: v.product_id,
            imgUrl: v.image,
          });
          console.log( v.price)
        }

        //截取六宫格长度
        for (let i = 0; i < data.data.data.good_list.length; i += this.once) {
          let o = {
            tpyer: data.data.data.good_list.slice(i, i + this.once),
          };
          this.Newarr.push(o);
        }
      } else {
        this.$toast("产品信息错误");
      }
    });
  },
  methods: {
    onChange(index) {
      this.current = index;
    },
    onClickIcon() {},
    toCart(){
      this.$router.push("/cart")
    },
    onClickAdd() {
      this.show = true;
    },
    onClickBuy() {},
    onBuyClicked() {},
    onAddCartClicked(data) {
      this.show = false;
      this.$axios
        .post("/api/cart/add", {
          cartNum: data.selectedNum, //添加数量
          new: 0,
          productId: data.selectedSkuComb.goods_id, //产品ID
          uniqueId: data.selectedSkuComb.id,
        })
        .then((d) => {
          if (d.data.status != 200) {
            this.$router.push("/login");
            console.log(d);
          }
          this.$toast("成功加入购物车");
        });
    },
  },
};
</script>
<style lang="less" >
.detail {
  padding-bottom: 100px;
  background-color: #f5f5f5;
  //   商品样式
  .bannerD {
    background-color: #fff;
    .custom-indicator {
      position: absolute;
      right: 10px;
      bottom: 10px;
      padding: 2px 5px;
      font-size: 12px;
      color: #000;
      background: rgba(255, 255, 255, 1);
    }
    .infoName {
      .money {
        font-size: 1.5rem;
        color: #fc4141;
        font-weight: 700;
        padding-left: 0.5rem;
        span {
          padding-left: 0.5rem;
          font-size: 1rem;
          color: #000;
          img {
            width: 8vw;
            height: 2vh;
          }
        }
      }
      .info {
        font-size: 1rem;
        font-weight: 700;
        margin-left: 0.9rem;
      }
      .num {
        display: flex;
        margin-top: 1rem;
        font-size: 0.8rem;
        color: #aaa;
        padding-left: 1rem;
        padding-right: 1rem;
        padding-bottom: 1rem;

        div {
          flex: 1;
          &:nth-of-type(2) {
            text-align: center;
          }
          &:last-of-type {
            text-align: right;
          }
        }
      }
    }
  }
  //   评论样式
  .evaluate {
    display: flex;
    justify-content: space-between;
    margin-top: 1rem;
    margin-bottom: 1rem;
    padding-left: 0.5rem;
    padding-right: 0.5rem;
    background-color: #fff;
    height: 3rem;
    line-height: 3rem;
    div {
      height: 3rem;
      line-height: 3rem;
      .redtext {
        color: #f00;
      }
      .arroeR {
        height: 3rem;
        line-height: 3rem;
      }
    }
  }
  //   推荐样式
  .recommend {
    background-color: #fff;
    .good {
      padding-top: 0.8rem;
      text-align: center;
      background-image: linear-gradient(90deg, #f57a37 0, #f21b07);
      -webkit-background-clip: text;
      font-size: 1.2rem;
      -webkit-text-fill-color: transparent;
    }
    .listBanner {
      .my-swipe .van-swipe-item {
        color: #fff;
        font-size: 20px;
        line-height: 150px;
        text-align: center;
        background-color: #f5f5f5;
      }
    }
    .bItem {
      display: flex;
      flex-wrap: wrap;
      justify-content: center;

      .boxItem {
        width: 27.3%;
        height: 27.3%;
        margin: 2vw;
        .boxImg {
          width: 30vw;
          height: 30vw;
        }
        .line {
          text-overflow: ellipsis;
          white-space: nowrap;
          overflow: hidden;
          margin: 0;
        }
        .linet {
          margin-top: 0;
          color: #f00;
        }
      }
    }
  }
  //   详情样式
  .details {
    width: 100%;
    overflow: hidden;
    .bigtitle {
      background-color: #fff;
      height: 6vh;
      line-height: 6vh;
      text-align: center;
    }
    .conter {
      width: 100%;
      overflow: hidden;
      img {
        width: 100vw !important;
        display: block !important;
      }
    }
  }
}
</style>